import React, { useEffect, useState } from 'react';
import axios from 'axios';
import '../../css/content-ctn3.css';
import Accordion from 'react-bootstrap/Accordion';
import Modal from 'react-modal';
import Button from '../../components/Button_.js';
import { ButtonContainer,DeleteModalStyles } from '../../css/componunt/Modalcss.js';


function Resetdata() {

  //
  const deleteModalShow = () => setDeleteModalOpen(true);
  const deleteModalClose = () => setDeleteModalOpen(false);
  const resetModalShow = () => setResetModalOpen(true);
  const resetModalClose = () => setResetModalOpen(false);


  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  const [resetModalOpen, setResetModalOpen] = useState(false);
  const [MenuTreeList, setMenuTreeList] = useState([]); //  
  const [DataResetDatail, setDataResetDatail] = useState({
    init_id:'-',
    menu_id:'-',
    menu_name:'-',
    table_id:'-',
    data_ctn:'-',
    init_sql:'-',
    create_dt:'-',
    create_user:'-',
    modify_dt:'-',
    modify_user:'-'                    
  }); // 
  const [current_cnt, setCurrent_cnt] = useState('-'); //ID  useState
  const [menuInfo, setMenuInfo] = useState({ menu_id: '', menu_name: '' }); //ID  useState

  const handleMenuClick = (menu_id, menu_name) => {
    setMenuInfo({menu_id, menu_name});
  };

  //  api   
  const fetchMenuTree = async () => {
    try {
      const response = await axios.get(`http://localhost:8080/api/admin/menuMenegement`);
      setMenuTreeList(response.data.menuTreeList);

    } catch (error) {
      console.error('Error fetching menu list:', error);
    }
  };

  //  api   
  const fetchInitDataDetail = async () => {
    try {
      const response = await axios.get(`http://localhost:8080/api/maintenance/resetdata/${menuInfo.menu_id}`);
      setDataResetDatail(response.data.initDataDetail);
      setCurrent_cnt(response.data.current_cnt);
      
      if(response.data.details){
        alert(response.data.details);
      }

    } catch (error) {
      console.error('Error fetching menu list:', error);
    }
  };

  //       1 
  useEffect(() => {
    fetchMenuTree();
  }, []);

  //id     
  useEffect(() => {
    fetchInitDataDetail();
  }, [menuInfo]);

  // api  
  const handleDelete = async () => {
    try {
      const response = await axios.delete(`http://localhost:8080/api/maintenance/resetdata/${menuInfo.menu_id}`);

        alert(response.data.details);
        //    (:  ,   )
        fetchInitDataDetail();  //  
        deleteModalClose();
      
    } catch (error) {
      console.error('   :', error);
      alert('     .');
    }
  };

  // api  
  const handleReset = async () => {
    try {
      const response = await axios.put(`http://localhost:8080/api/maintenance/resetdata/${menuInfo.menu_id}`);

        alert(response.data.details);
        //    (:  ,   )
        fetchInitDataDetail();  //  
        resetModalClose();
      
    } catch (error) {
      console.error('   :', error);
      alert('     .');
    }
  };

  return (
    <div>
      <div className="tittle-ctn3">
        <div></div>
      </div>
      <div className="maincontents-ctn3">
      <div className="left-tree-ctn3">
          <div className="title-div-ctn3"><span></span></div>
          <div className="contents-tree-ctn3">
          <Accordion alwaysOpen>
            {MenuTreeList.map((menu, index) => (
                <Accordion.Item eventKey={index.toString()} key={menu.top_menu_id}>
                    <Accordion.Header >{menu.top_menu_name}</Accordion.Header>
                    <Accordion.Body>
                        {menu.child_munu_name_list.length > 0 ? (
                            menu.child_munu_name_list.map((childMenu, childIndex) => (
                                <div key={menu.child_menu_id_list[childIndex]}>
                                    <span onClick={() => handleMenuClick(menu.child_menu_id_list[childIndex],childMenu)}>{childMenu}</span>
                                    {childIndex < menu.child_munu_name_list.length - 1 && <hr />}
                                </div>
                            ))
                        ) : (
                            null
                        )}
                    </Accordion.Body>
                </Accordion.Item>
            ))}
          </Accordion>
          </div>
        </div>
        <div className="right-details-ctn3">
          <div className="title-div-ctn3"><span> </span><Button primary onClick={deleteModalShow}></Button> <Button primary onClick={resetModalShow}></Button></div>
          <div className="contents-details-ctn3">
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> ID</div>
              <div className="item-value-ctn3">{DataResetDatail && DataResetDatail.init_id ? DataResetDatail.init_id : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> ID</div>
              <div className="item-value-ctn3">{DataResetDatail && DataResetDatail.menu_id ? DataResetDatail.menu_id : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> </div>
              <div className="item-value-ctn3">{DataResetDatail && DataResetDatail.menu_name ? DataResetDatail.menu_name : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> </div>
              <div className="item-value-ctn3">{DataResetDatail && DataResetDatail.table_id ? DataResetDatail.table_id : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3">  </div>
              <div className="item-value-ctn3">{DataResetDatail && DataResetDatail.data_ctn ? DataResetDatail.data_ctn : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3">  </div>
              <div className="item-value-ctn3">{current_cnt !== null && current_cnt !== undefined ? current_cnt : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> SQL</div>
            </div>
            <div className="resetSQL">{DataResetDatail && DataResetDatail.init_sql ? (<pre>{DataResetDatail.init_sql}</pre>) : '-'}</div>
          </div>
        </div>

      </div>

      {/*   */}
      <Modal
        isOpen={deleteModalOpen}
        onRequestClose={deleteModalClose}
        style={DeleteModalStyles}
        ariaHideApp={false}
        contentLabel="Pop up Message"
        shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2></h2>
          {menuInfo.menu_name}   ?
          <ButtonContainer>
            <Button primary onClick={handleDelete}></Button>
            <Button onClick={deleteModalClose}></Button>
          </ButtonContainer>
        </div>
      </Modal>

      {/*   */}
      <Modal
        isOpen={resetModalOpen}
        onRequestClose={resetModalClose}
        style={DeleteModalStyles}
        ariaHideApp={false}
        contentLabel="Pop up Message"
        shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2></h2>
          {menuInfo.menu_name}   ?
          <ButtonContainer>
            <Button primary onClick={handleReset}></Button>
            <Button onClick={resetModalClose}></Button>
          </ButtonContainer>
        </div>
      </Modal>
      
      
    </div>
  );
}

export default Resetdata;