import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import { Button } from 'react-bootstrap';
import '../../css/content-ctn2.css';
import Resizer from '../../components/ResizerRow'; // Resizer  
import Pagination from '../../components/Pagination';
import '../../css/mainTable/UserManagement.css'
import UserDetailTable from '../../detailtap/UserDetailTable';
import RoleListTable from '../../detailtap/RoleListTable.js';

import { GridContainer, FormItem, Label, Input, ButtonContainer, InsertModalStyles, DeleteModalStyles, Required, LabelDiv, Select  } from '../../css/componunt/Modalcss.js';
import Modal from 'react-modal';
import Tab from 'react-bootstrap/Tab';
import Tabs from 'react-bootstrap/Tabs';



function UserManagement() {
  
  //  
  const [insertModalOpen, setInsertModalOpen] = useState(false);
  const [updateModalOpen, setUpdateModalOpen] = useState(false);
  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  const [passwdInitModalOpen, setPasswdInitModalOpen] = useState(false);
  const [useynModalOpen, setUseynModalOpen] = useState(false);
  
  const insertModalShow = () => setInsertModalOpen(true);
  const insertModalClose = () => setInsertModalOpen(false);
  const updateModalShow = (user_id) => {
    if (user_id=="-") {
      alert("  ."); 
      return; 
    }
    setUpdateModalOpen(true); 
  };
  const updateModalClose = () => setUpdateModalOpen(false);
  const deleteModalShow = (user_id) => {
    if (user_id=="-") {
      alert("  ."); //  
      return; //  
    }
    setDeleteModalOpen(true);
  };
  const deleteModalClose = () => setDeleteModalOpen(false);

  const passwdInitModalShow = (user_id) => {
    if (user_id=="-") {
      alert(" ."); //  
      return; //  
    }
    setPasswdInitModalOpen(true);
  };
  const passwdInitModalClose = () => setPasswdInitModalOpen(false);

  const useynyModalShow = (user_id,user_use_yn) => {
    if (user_id=="-") {
      alert(" ."); //  
      return; //  
    }
    if (user_use_yn=="Y") {
      alert("  Y."); //  
      return; //  
    }
    setUseynModalOpen(true);
  };

  const useynnModalShow = (user_id,user_use_yn) => {
    if (user_id=="-") {
      alert(" ."); //  
      return; //  
    }
    if (user_use_yn=="N") {
      alert("  N"); //  
      return; //  
    }
    setUseynModalOpen(true);
  };
  const useynModalClose = () => setUseynModalOpen(false);


  

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setUserDetail({
        ...UserDetail,
        [name]: value,
    });
  };

  // api  
  const handleInsert = async (event) => {
    event.preventDefault();

    const userName = event.target.user_name.value.trim();
    const userAccountId = event.target.user_account_id.value.trim();
    const user_use_yn = event.target.user_use_yn.checked ? 'Y' : 'N'; //   
    if (!userName) {
      alert(' .')
      return;
    }

    if (!userAccountId) {
      alert('ID .');
      return;
    }

     //    ID 
     const currentSelectedRoles = Array.from(
      document.querySelectorAll('input.insertrolecheckbox[type="checkbox"]:checked')
    ).map((checkbox) => Number(checkbox.value));

    //   
    const data = {
      user_name: userName,
      user_account_id: userAccountId,
      user_use_yn: user_use_yn,
      user_category: event.target.user_category.value,
      user_department: event.target.user_department.value,
      user_phon_no: event.target.user_phon_no.value,
      user_email: event.target.user_email.value,
      rel_user_roleID: currentSelectedRoles //   
    };
    try {
      alert(JSON.stringify(data, null, 2)); // 2   
      const response = await axios.post('http://localhost:8080/api/admin/user', data);
      alert(response.data.resultmessage);
      insertModalClose();
      fetchUserList(currentPage, itemsPerPage, filterList);  //  
    } catch (error) {
      console.error('     :', error);
      alert('      .');
    }
  };

  // api  
  const handleUpdate = async (event) => {
    event.preventDefault();

    //    ID 
    const currentSelectedRoles = Array.from(
      document.querySelectorAll('input.updaterolecheckbox[type="checkbox"]:checked')
    ).map((checkbox) => Number(checkbox.value));
    
    //   
    const user_use_yn = event.target.user_use_yn.checked ? 'Y' : 'N'; //   
    

    //   
    const data = {
      user_name: event.target.user_name.value,
      user_account_id: event.target.user_account_id.value,
      user_use_yn: user_use_yn,
      user_category: event.target.user_category.value,
      user_department: event.target.user_department.value,
      user_phon_no: event.target.user_phon_no.value,
      user_email: event.target.user_email.value,
      rel_user_roleID: currentSelectedRoles //   
    };

    try {
      alert(JSON.stringify(data, null, 2)); // 2   
      const response = await axios.put(`http://localhost:8080/api/admin/user/${UserDetail.user_id}`, data);
      alert(response.data.resultmessage);
      updateModalClose();
      fetchUserList(currentPage, itemsPerPage, filterList);
      fetchUserDetail();  
    } catch (error) {
      console.error('     :', error);
      alert('      .');
    }
  };

  // api  
  const handleDelete = async () => {
    try {
      const response = await axios.delete(`http://localhost:8080/api/admin/user/${UserDetail.user_id}`);
      alert(response.data.resultmessage);
      fetchUserList(currentPage, itemsPerPage, filterList);
      fetchUserDetail();    //  
      deleteModalClose();
    } catch (error) {
      console.error('    :', error);
      alert('     .');
    }
  };

  // api  
  const handlePasswdInit = async () => {
    try {
      const response = await axios.patch(`http://localhost:8080/api/admin/user/passwdinit/${UserDetail.user_id}`);
      alert(response.data.resultmessage);
      fetchUserList(currentPage, itemsPerPage, filterList);  //  
      fetchUserDetail(); 
      passwdInitModalClose(); 
    } catch (error) {
      console.error('    :', error);
      alert('     .');
    }
  };

  // api  
  const handleUseyn = async () => {
    try {
      const response = await axios.patch(`http://localhost:8080/api/admin/user/useyn/${UserDetail.user_id}`);
      alert(response.data.resultmessage);
      fetchUserList(currentPage, itemsPerPage, filterList);  //  
      fetchUserDetail(); 
      useynModalClose(); 
    } catch (error) {
      console.error('    :', error);
      alert('     .');
    }
  };

  //  

  const containerRef = useRef(null);
  const topDivRef = useRef(null);
  const bottomDivRef = useRef(null);

  const [userList, setUserList] = useState([]);
  const [otherInformation, setOtherInformation] = useState({});
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(50);
  const [isFirstLoad, setIsFirstLoad] = useState(true);
  const [user_id, setUser_id] = useState('');

  const [filterList, setFilterList] = useState({user_name:'',user_account_id:''});
  const [UserDetail, setUserDetail] = useState({        //  
    user_id:'-',                         
    user_name:'-',                       
    user_account_id:'-',                 
    user_passwd:'-',                     
    user_passwd_fail_cnt:'-',            
    user_use_yn:'-',
    user_category:'-',                     
    user_department:'-',                 
    user_phon_no:'-',                    
    user_email:'-',                      
    create_dt:'-',                       
    create_user:'-',                     
    modify_dt:'-',                       
    modify_user:'-',                                     
  }); 

  const [roleList, setRoleList] = useState([]);
  const [relUserRoleList, setRelUserRoleList] = useState([]);

  //     id 
  const [selectedRow, setSelectedRow] = useState(null);

  const handleRowClick = (index, user_id) => {
    setUser_id(user_id)
    setSelectedRow(index);
  };

  //id     
  useEffect(() => {
    fetchUserDetail();
  }, [user_id]);



  //  
  const fetchUserList = async (page_no, page_cnt, filterList) => {
    try {
      //  URL 
      let url = `http://localhost:8080/api/admin/user?page_no=${page_no}&page_cnt=${page_cnt}`;

      if (filterList.user_name !== undefined && filterList.user_name !== null && filterList.user_name.trim() !== '') {
        url += `&user_name=${encodeURIComponent(filterList.user_name)}`;
      }
      if (filterList.user_account_id !== undefined && filterList.user_account_id !== null && filterList.user_account_id.trim() !== '') {
        url += `&user_account_id=${encodeURIComponent(filterList.user_account_id)}`;
      }
      const response = await axios.get(url);
      setUserList(response.data.userList);
      setOtherInformation(response.data.otherInformation);
      setIsFirstLoad(false);
    } catch (error) {
      console.error('Error fetching user list:', error);
    }
  };

  //  api   
  const fetchUserDetail = async () => {
    try {
      const response = await axios.get(`http://localhost:8080/api/admin/user/${user_id}`);
      setUserDetail(response.data.userDetail);
      setRelUserRoleList(response.data.relUserRoleList);
    } catch (error) {
      console.error('Error fetching user list:', error);
    }
  };


  useEffect(() => {
    if (isFirstLoad) {
      return;
    }
    fetchUserList(currentPage, itemsPerPage, filterList );
  }, [currentPage,itemsPerPage]); 

  const handlePageChange = (newPage) => {
    if (newPage > 0 && newPage <= otherInformation.max_page_no || 1) {
      setCurrentPage(newPage);
    }
  };

  const handleItemsPerPageChange = (event) => {
    setItemsPerPage(parseInt(event.target.value, 10));
    setCurrentPage(1);
  };

  const handleSearch = () => {
    fetchUserList(currentPage, itemsPerPage, filterList);
  };

  //  
  const fetchRoleList = async () => {
    try {
      //  URL 
      let url = `http://localhost:8080/api/admin/role`;
      const response = await axios.get(url);
      setRoleList(response.data.roleList);
    } catch (error) {
      console.error('Error fetching role list:', error);
    }
  };

  useEffect(() => {
    fetchRoleList();
  }, []); 

  const handleUserNameChange = (e) => {
    setFilterList(prevState => ({ ...prevState, user_name: e.target.value }));
  };

  const handleUserAccountIdChange = (e) => {
    setFilterList(prevState => ({ ...prevState, user_account_id: e.target.value }));
  };

  const isRoleChecked = (roleId) => {
    return relUserRoleList.some(item => item.role_id === roleId);
  };


  return (
    <div>
      <div className="content_container-ctn2" ref={containerRef}>
        <div className="title-ctn2"><div></div></div>
        <div className="cud_button-ctn2">
          <Button primary onClick={insertModalShow}></Button>
          <Button primary onClick={() => updateModalShow(UserDetail.user_id)} ></Button> 
          <Button primary onClick={() => deleteModalShow(UserDetail.user_id)}></Button> 
          <Button primary onClick={() => passwdInitModalShow(UserDetail.user_id)}></Button> 
          <Button primary onClick={() => useynyModalShow(UserDetail.user_id,UserDetail.user_use_yn)}></Button> 
          <Button primary onClick={() => useynnModalShow(UserDetail.user_id,UserDetail.user_use_yn)}></Button>
        </div>
        <div className="read_button-ctn2" id="">
          <div id="left">
             : <input type="text" placeholder=" " onChange={handleUserNameChange} />
            ID : <input type="text" placeholder="ID " onChange={handleUserAccountIdChange} />
          </div>
          <div id="right"><Button onClick={handleSearch}></Button></div>
        </div>
        <div className="maincontent-ctn2" id="" >
          <table className ="MainTable">
            <thead className="TableHeader">
              <tr>
                <th className="col1"></th>
                <th className="col2"></th>
                <th className="col3">ID</th>
                <th className="col4"></th>
                <th className="col5"></th>
                <th className="col6"></th>
                <th className="col7"></th>
                <th className="col8"></th>
                <th className="col9"></th>
                <th className="col10"></th>
              </tr>
            </thead>
            <tbody className="TableBody-ctn2" id ="TableBody" ref={topDivRef}>
              {userList.map((user, index) => (
                <tr key={user.user_id} onClick={() => handleRowClick(index, user.user_id)} className={selectedRow === index ? 'selected' : ''}>
                  <td className="col1">{(currentPage - 1) * itemsPerPage + index + 1}</td> {/* 1    */}
                  <td className="col2">{user.user_name}</td>
                  <td className="col3">{user.user_account_id}</td>
                  <td className="col4">{user.user_use_yn}</td>
                  <td className="col5">{user.user_category}</td>
                  <td className="col6">{user.user_passwd_fail_cnt}</td>
                  <td className="col7">{user.create_user}</td>
                  <td className="col8">{new Date(user.create_dt).toLocaleString()}</td>
                  <td className="col9">{user.modify_user}</td>
                  <td className="col10">{user.modify_dt ? new Date(user.modify_dt).toLocaleString() : ''}</td>
                  
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        <Pagination currentPage={currentPage} totalPages={otherInformation.max_page_no || 1} onPageChange={handlePageChange} onItemsPerPageChange={handleItemsPerPageChange} itemsPerPage={itemsPerPage} totalItems={otherInformation} isFirstLoad={isFirstLoad}/>
        <Resizer containerRef={containerRef} topDivRef={topDivRef} bottomDivRef={bottomDivRef} />
        <div className="detailstap-ctn2" ref={bottomDivRef}>
          <Tabs defaultActiveKey="" id="uncontrolled-tab-example" className="mb-3">
            <Tab eventKey="" title=""><UserDetailTable UserDetail={UserDetail} /></Tab>
            <Tab eventKey="" title=""><RoleListTable roleList={roleList} relUserRoleList={relUserRoleList} /></Tab>
          </Tabs>
        </div>
      </div>


      {/*   */}
      <Modal isOpen={insertModalOpen} 
             onRequestClose={insertModalClose} 
             style={InsertModalStyles} 
             ariaHideApp={false} 
             contentLabel="Pop up Message" 
             shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2>  </h2>
          <form onSubmit={handleInsert}>
            <GridContainer>
                <FormItem key="1">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="" name="user_name"/>
                </FormItem>
                <FormItem key="2">
                  <LabelDiv>
                    <Label htmlFor="ID">ID</Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="ID" name="user_account_id"/>
                </FormItem>
                <FormItem key="3">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Input type="checkbox" id="" name="user_use_yn" defaultChecked/>
                </FormItem>
                <FormItem key="4">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Select id="" name="user_category" defaultValue="">
                      <option value=""></option>
                      <option value=""></option>
                    </Select>
                </FormItem>
                <FormItem key="5">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Input type="text" id="" name="user_department"/>
                </FormItem>
                <FormItem key="6">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                  <Input type="text" id="" name="user_phon_no"/>
                </FormItem>
                <FormItem key="7">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Input type="text" id="" name="user_email"/>
                </FormItem>
            </GridContainer>
            <table className ="MainTable">
            <thead className="TableHeader">
              <tr>
                <th className="col1"></th>
                <th className="col2"></th>
                <th className="col3"></th>
              </tr>
            </thead>
            <tbody className="TableBody-ctn2">
              {roleList.map((role) => (
                <tr key={role.role_id}>
                  <td className="col1"><input className='insertrolecheckbox' type='checkbox' value={role.role_id} /></td> 
                  <td className="col2">{role.role_code}</td>
                  <td className="col3">{role.role_name}</td>
                </tr>
              ))}
            </tbody>
          </table>
            <ButtonContainer>
              <Button primary type="submit"></Button>
              <Button onClick={insertModalClose}></Button>
            </ButtonContainer>
          </form>
        </div>
      </Modal>


      {/*   */}
      <Modal
        isOpen={updateModalOpen}
        onRequestClose={updateModalClose}
        style={InsertModalStyles}
        ariaHideApp={false}
        contentLabel="Pop up Message"
        shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2>  </h2>
          <form onSubmit={handleUpdate}>
            <GridContainer>
                <FormItem key="1">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="" name="user_name" disabled value={UserDetail.user_name}/>
                </FormItem>
                <FormItem key="2">
                  <LabelDiv>
                    <Label htmlFor="ID">ID</Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="ID" name="user_account_id" disabled value={UserDetail.user_account_id}/>
                </FormItem>
                <FormItem key="3">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Input type="checkbox" id="" name="user_use_yn" defaultChecked={UserDetail.user_use_yn === 'Y'}/>
                </FormItem>
                <FormItem key="4">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Select id="" name="user_category" defaultValue={UserDetail.user_category}>
                      <option value=""></option>
                      <option value=""></option>
                    </Select>
                </FormItem>
                <FormItem key="5">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Input type="text" id="" name="user_department" value={UserDetail.user_department} onChange={handleInputChange}/>
                </FormItem>
                <FormItem key="6">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                  <Input type="text" id="" name="user_phon_no" value={UserDetail.user_phon_no} onChange={handleInputChange}/>
                </FormItem>
                <FormItem key="7">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Input type="text" id="" name="user_email" value={UserDetail.user_email} onChange={handleInputChange}/>
                </FormItem>
            </GridContainer>
            <table className ="MainTable">
            <thead className="TableHeader">
              <tr>
                <th className="col1"></th>
                <th className="col2"></th>
                <th className="col3"></th>
              </tr>
            </thead>
            <tbody className="TableBody-ctn2">
              {roleList.map((role) => (
                <tr key={role.role_id}>
                  <td className="col1"><input className='updaterolecheckbox' type='checkbox' value={role.role_id} defaultChecked={isRoleChecked(role.role_id)} /></td> 
                  <td className="col2">{role.role_code}</td>
                  <td className="col3">{role.role_name}</td>
                </tr>
              ))}
            </tbody>
          </table>
            <ButtonContainer>
              <Button primary type="submit"></Button>
              <Button onClick={updateModalClose}></Button>
            </ButtonContainer>
          </form>
        </div>
      </Modal>

      {/*   */}
      <Modal
        isOpen={deleteModalOpen}
        onRequestClose={deleteModalClose}
        style={DeleteModalStyles}
        ariaHideApp={false}
        contentLabel="Pop up Message"
        shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2> </h2>
          "{UserDetail.user_name}" "{UserDetail.user_account_id}"  ?
          <ButtonContainer>
            <Button primary onClick={handleDelete}></Button>
            <Button onClick={deleteModalClose}></Button>
          </ButtonContainer>
        </div>
      </Modal>

      {/*   */}
      <Modal
        isOpen={passwdInitModalOpen}
        onRequestClose={passwdInitModalClose}
        style={DeleteModalStyles}
        ariaHideApp={false}
        contentLabel="Pop up Message"
        shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2>  </h2>
          "{UserDetail.user_name}" "{UserDetail.user_account_id}"   ?
          <ButtonContainer>
            <Button primary onClick={handlePasswdInit}></Button>
            <Button onClick={passwdInitModalClose}></Button>
          </ButtonContainer>
        </div>
      </Modal>

      {/* yn  */}
      <Modal
        isOpen={useynModalOpen}
        onRequestClose={useynModalClose}
        style={DeleteModalStyles}
        ariaHideApp={false}
        contentLabel="Pop up Message"
        shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2> </h2>
          "{UserDetail.user_name}" "{UserDetail.user_account_id}"  ?
          <ButtonContainer>
            <Button primary onClick={handleUseyn}></Button>
            <Button onClick={useynModalClose}></Button>
          </ButtonContainer>
        </div>
      </Modal>






    </div>
    
  );
}

export default UserManagement;