import React, { useState, useRef, useEffect } from 'react';
import axios from 'axios';
import { Button } from 'react-bootstrap';
import '../../css/content-ctn2.css';
import Resizer from '../../components/ResizerRow'; // Resizer  
import Pagination from '../../components/Pagination';
import '../../css/mainTable/RoleManagement.css'
import RoleDetailTable from '../../detailtap/RoleDetailTable';
import { GridContainer, FormItem, Label, Input, ButtonContainer, InsertModalStyles, DeleteModalStyles, Required, LabelDiv } from '../../css/componunt/Modalcss.js';
import Modal from 'react-modal';

function RoleManagement() {
  
  //  
  const [insertModalOpen, setInsertModalOpen] = useState(false);
  const [updateModalOpen, setUpdateModalOpen] = useState(false);
  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  
  const insertModalShow = () => setInsertModalOpen(true);
  const insertModalClose = () => setInsertModalOpen(false);
  const updateModalShow = (role_id) => {
    if (role_id=="-") {
      alert("  ."); 
      return; 
    }
    setUpdateModalOpen(true); 
  };
  const updateModalClose = () => setUpdateModalOpen(false);
  const deleteModalShow = (role_id) => {
    if (role_id=="-") {
      alert("  ."); //  
      return; //  
    }
    setDeleteModalOpen(true);
  };

  const deleteModalClose = () => setDeleteModalOpen(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setRoleDetail({
        ...RoleDetail,
        [name]: value,
    });
  };

  // api  
  const handleInsert = async (event) => {
    event.preventDefault();

    const roleCode = event.target.role_code.value.trim();
    const roleName = event.target.role_name.value.trim();
    const meta_use_yn = event.target.meta_use_yn.checked ? 'Y' : 'N'; //   
    if (!roleCode) {
      alert(' .')
      return;
    }

    if (!roleName) {
      alert(' .');
      return;
    }

    const formData = new FormData(event.target);
    formData.set('meta_use_yn', meta_use_yn); //   'Y'  'N' 

    const data = Object.fromEntries(formData.entries());
    try {
      const response = await axios.post('http://localhost:8080/api/admin/role', data);
      alert(response.data.resultmessage);
      insertModalClose();
      fetchRoleList(currentPage, itemsPerPage, filterList);  //  
    } catch (error) {
      console.error('     :', error);
      alert('      .');
    }
  };

  // api  
  const handleUpdate = async (event) => {
    event.preventDefault();

    const roleCode = event.target.role_code.value.trim();
    const roleName = event.target.role_name.value.trim();
    const meta_use_yn = event.target.meta_use_yn.checked ? 'Y' : 'N'; //   
    if (!roleCode) {
      alert(' .')
      return;
    }

    if (!roleName) {
      alert(' .');
      return;
    }

    const formData = new FormData(event.target);
    formData.set('meta_use_yn', meta_use_yn); //   'Y'  'N' 

    const data = Object.fromEntries(formData.entries());
    try {
      const response = await axios.put(`http://localhost:8080/api/admin/role/${RoleDetail.role_id}`, data);
      alert(response.data.resultmessage);
      updateModalClose();
      fetchRoleList(currentPage, itemsPerPage, filterList);  
    } catch (error) {
      console.error('     :', error);
      alert('      .');
    }
  };

  // api  
  const handleDelete = async () => {
    try {
      const response = await axios.delete(`http://localhost:8080/api/admin/role/${RoleDetail.role_id}`);
      alert(response.data.resultmessage);
      fetchRoleList(currentPage, itemsPerPage, filterList);  //  
      deleteModalClose();
    } catch (error) {
      console.error('    :', error);
      alert('     .');
    }
  };

  //  

  const containerRef = useRef(null);
  const topDivRef = useRef(null);
  const bottomDivRef = useRef(null);

  const [roleList, setRoleList] = useState([]);
  const [otherInformation, setOtherInformation] = useState({});
  const [currentPage, setCurrentPage] = useState(1);
  const [itemsPerPage, setItemsPerPage] = useState(50);
  const [isFirstLoad, setIsFirstLoad] = useState(true);
  const [role_id, setRole_id] = useState('');

  const [filterList, setFilterList] = useState({role_name:''});
  const [RoleDetail, setRoleDetail] = useState({        //  
    role_id:'-',
    role_code:'-',
    role_name:'-',
    meta_use_yn:'-',
    create_dt:'-',
    create_user:'-',
    modify_dt:'-',
    modify_user:'-'                    
  }); 

  //     id 
  const [selectedRow, setSelectedRow] = useState(null);

  const handleRowClick = (index, role_id) => {
    setRole_id(role_id)
    setSelectedRow(index);
  };

  //id     
  useEffect(() => {
    fetchRoleDetail();
  }, [role_id]);



  //  
  const fetchRoleList = async (page_no, page_cnt, filterList) => {
    try {
      //  URL 
      let url = `http://localhost:8080/api/admin/role?page_no=${page_no}&page_cnt=${page_cnt}`;
      // role_name       URL 
      if (filterList.role_name !== undefined && filterList.role_name !== null && filterList.role_name.trim() !== '') {
        url += `&role_name=${encodeURIComponent(filterList.role_name)}`;
      }
      const response = await axios.get(url);
      setRoleList(response.data.roleList);
      setOtherInformation(response.data.otherInformation);
      setIsFirstLoad(false);
    } catch (error) {
      console.error('Error fetching role list:', error);
    }
  };

  //  api   
  const fetchRoleDetail = async () => {
    try {
      const response = await axios.get(`http://localhost:8080/api/admin/role/${role_id}`);
      setRoleDetail(response.data.roleDetail);

    } catch (error) {
      console.error('Error fetching role list:', error);
    }
  };


  useEffect(() => {
    if (isFirstLoad) {
      return;
    }
    fetchRoleList(currentPage, itemsPerPage, filterList );
  }, [currentPage,itemsPerPage]); 

  const handlePageChange = (newPage) => {
    if (newPage > 0 && newPage <= otherInformation.max_page_no || 1) {
      setCurrentPage(newPage);
    }
  };

  const handleItemsPerPageChange = (event) => {
    setItemsPerPage(parseInt(event.target.value, 10));
    setCurrentPage(1);
  };

  const handleSearch = () => {
    fetchRoleList(currentPage, itemsPerPage, filterList);
  };


  return (
    <div>
      <div className="content_container-ctn2" ref={containerRef}>
        <div className="title-ctn2"><div></div></div>
        <div className="cud_button-ctn2"><Button onClick={insertModalShow}></Button> <Button primary onClick={() => updateModalShow(RoleDetail.role_id)} ></Button> <Button primary onClick={() => deleteModalShow(RoleDetail.role_id)}></Button></div>
        <div className="read_button-ctn2" id="">
          <div id="left"> : <input type="text" placeholder=" " onChange={(e) => setFilterList({ role_name: e.target.value })} /></div>
          <div id="right"><Button onClick={handleSearch}></Button></div>
        </div>
        <div className="maincontent-ctn2" id="" >
          <table className ="MainTable">
            <thead className="TableHeader">
              <tr>
                <th className="role-num"></th>
                <th className="role-code"></th>
                <th className="role-name"></th>
                <th className="role-createuser"></th>
                <th className="role-createdt"></th>
                <th className="role-modifyuser"></th>
              </tr>
            </thead>
            <tbody className="TableBody-ctn2" id ="TableBody" ref={topDivRef}>
              {roleList.map((role, index) => (
                <tr key={role.role_id} onClick={() => handleRowClick(index, role.role_id)} className={selectedRow === index ? 'selected' : ''}>
                  <td className="role-num">{(currentPage - 1) * itemsPerPage + index + 1}</td> {/* 1    */}
                  <td className="role-code">{role.role_code}</td>
                  <td className="role-name">{role.role_name}</td>
                  <td className="role-createuser">{role.create_user}</td>
                  <td className="role-createdt">{new Date(role.create_dt).toLocaleString()}</td>
                  <td className="role-modifyuser">{role.modify_user}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
        <Pagination currentPage={currentPage} totalPages={otherInformation.max_page_no || 1} onPageChange={handlePageChange} onItemsPerPageChange={handleItemsPerPageChange} itemsPerPage={itemsPerPage} totalItems={otherInformation} isFirstLoad={isFirstLoad}/>
        <Resizer containerRef={containerRef} topDivRef={topDivRef} bottomDivRef={bottomDivRef} />
        <div className="detailstap-ctn2" ref={bottomDivRef}>
          <div></div>
          <RoleDetailTable RoleDetail={RoleDetail} />
        </div>
      </div>


      {/*   */}
      <Modal isOpen={insertModalOpen} 
             onRequestClose={insertModalClose} 
             style={InsertModalStyles} 
             ariaHideApp={false} 
             contentLabel="Pop up Message" 
             shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2>  </h2>
          <form onSubmit={handleInsert}>
            <GridContainer>
                <FormItem key="1">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="" name="role_code"/>
                </FormItem>
                <FormItem key="2">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="" name="role_name"/>
                </FormItem>
                <FormItem key="3">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Input type="checkbox" id="" name="meta_use_yn"/>
                </FormItem>
            </GridContainer>
            <ButtonContainer>
              <Button primary type="submit"></Button>
              <Button onClick={insertModalClose}></Button>
            </ButtonContainer>
          </form>
        </div>
      </Modal>


      {/*   */}
      <Modal
        isOpen={updateModalOpen}
        onRequestClose={updateModalClose}
        style={InsertModalStyles}
        ariaHideApp={false}
        contentLabel="Pop up Message"
        shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2>  </h2>
          <form onSubmit={handleUpdate}>
            <GridContainer>
                <FormItem key="1">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="" name="role_code" value={RoleDetail.role_code} onChange={handleInputChange}/>
                </FormItem>
                <FormItem key="2">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="" name="role_name" value={RoleDetail.role_name} onChange={handleInputChange}/>
                </FormItem>
                <FormItem key="3">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Input type="checkbox" id="" name="meta_use_yn" value={RoleDetail.meta_use_yn} />
                </FormItem>
            </GridContainer>
            <ButtonContainer>
              <Button primary type="submit"></Button>
              <Button onClick={updateModalClose}></Button>
            </ButtonContainer>
          </form>
        </div>
      </Modal>

      {/*   */}
      <Modal
        isOpen={deleteModalOpen}
        onRequestClose={deleteModalClose}
        style={DeleteModalStyles}
        ariaHideApp={false}
        contentLabel="Pop up Message"
        shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2> </h2>
          "{RoleDetail.role_code}" "{RoleDetail.role_name}"  ?
          <ButtonContainer>
            <Button primary onClick={handleDelete}></Button>
            <Button onClick={deleteModalClose}></Button>
          </ButtonContainer>
        </div>
      </Modal>





    </div>
    
  );
}

export default RoleManagement;