import React, { useEffect, useState } from 'react';
import axios from 'axios';
import '../../css/content-ctn3.css';
import Accordion from 'react-bootstrap/Accordion';
import Modal from 'react-modal';
import Button from '../../components/Button_.js';
import { GridContainer, FormItem, Label, Input, ButtonContainer, InsertModalStyles, DeleteModalStyles, Required, LabelDiv } from '../../css/componunt/Modalcss.js';




function MenuManagement() {

  // useState
  const [insertModalOpen, setInsertModalOpen] = useState(false);
  const [updateModalOpen, setUpdateModalOpen] = useState(false);
  const [deleteModalOpen, setDeleteModalOpen] = useState(false);
  const [MenuTreeList, setMenuTreeList] = useState([]); //  
  const [MenuDetail, setMenuDetail] = useState({        //  
    menu_id:'-',
    menu_code:'-',
    menu_name:'-',
    menu_seq:'-',
    org_menu_id:'-',
    use_yn:'-',
    menu_url:'-',
    create_dt:'-',
    create_user:'-',
    modify_dt:'-',
    modify_user:'-'                    
  }); 
  const [menuSeq, setMenuSeq] = useState(''); //     useState
  const [orgMenuInfo, setOrgMenuInfo] = useState({ top_menu_id: '', top_menu_name: '' }); 
  const [menuInfo, setMenuInfo] = useState({ menu_id: '', menu_name: '' }); //ID  useState

  // ID  
  const handleTopMenuClick = (menu_id, menu_name, top_menu_id, top_menu_name) => {
    top_menu_id = menu_id;
    top_menu_name = menu_name;
    setOrgMenuInfo({ top_menu_id, top_menu_name });
    setMenuInfo({menu_id, menu_name});
  };

  const handleChildMenuClick = (menu_id, menu_name) => {
    setMenuInfo({menu_id, menu_name});
  };

  // 
  const insertModalShow = () => setInsertModalOpen(true);
  const insertModalClose = () => setInsertModalOpen(false);
  const updateModalShow = (menu_id) => {
    if (!menu_id) {
      alert("  ."); //  
      return; //  
    }
  
    setUpdateModalOpen(true); // menu_id   
  };
  const updateModalClose = () => setUpdateModalOpen(false);
  const deleteModalShow = (menu_id) => {
    if (!menu_id) {
      alert("  ."); //  
      return; //  
    }
    
    setDeleteModalOpen(true);}
  const deleteModalClose = () => setDeleteModalOpen(false);
  

  //  + 
  const handleMenuSeqChange_handleInputChange = (event) => {
    handleInputChange(event);
    handleMenuSeqChange(event);
  };

  // 
  const handleMenuSeqChange = (event) => {
    const value = event.target.value;

    //   
    if (isNaN(value) && value.trim() !== '') {
      alert('   .');
      setMenuSeq(''); //  
    } else {
      setMenuSeq(value); //     
    }
  };

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setMenuDetail({
        ...MenuDetail,
        [name]: value,
    });
  };

  //  api   
  const fetchMenuTree = async () => {
    try {
      const response = await axios.get(`http://localhost:8080/api/admin/menuMenegement`);
      setMenuTreeList(response.data.menuTreeList);

    } catch (error) {
      console.error('Error fetching menu list:', error);
    }
  };

  //       1 
  useEffect(() => {
    fetchMenuTree();
  }, []);

  //  api   
  const fetchMenuDetail = async () => {
    try {
      const response = await axios.get(`http://localhost:8080/api/admin/menuMenegement/${menuInfo.menu_id}`);
      setMenuDetail(response.data.menuDetail);

    } catch (error) {
      console.error('Error fetching menu list:', error);
    }
  };

  //id     
  useEffect(() => {
    fetchMenuDetail();
  }, [menuInfo]);


  // api  
  const handleInsert = async (event) => {
    event.preventDefault();

    const menuCode = event.target.menu_code.value.trim();
    const menuName = event.target.menu_name.value.trim();
    const menuUrl = event.target.menu_url.value.trim();
    const use_yn = event.target.use_yn.checked ? 'Y' : 'N'; //   
    if (!menuCode) {
      alert(' .')
      return;
    }

    if (!menuName) {
      alert(' .');
      return;
    }

    if (!menuUrl) {
      alert('URL .');
      return;
    }

    const formData = new FormData(event.target);
    formData.set('use_yn', use_yn); //   'Y'  'N' 
    formData.set('org_menu_id', orgMenuInfo.top_menu_id);
    const data = Object.fromEntries(formData.entries());
    try {
      const response = await axios.post('http://localhost:8080/api/admin/menuMenegement', data);
      console.log('Response:', response.data);
      alert(response.data.resultmessage);
      insertModalClose();
      fetchMenuTree();  //  
    } catch (error) {
      console.error('     :', error);
      alert('      .');
    }
  };

  // api  
  const handleUpdate = async (event) => {
    event.preventDefault();

    const menuCode = event.target.menu_code.value.trim();
    const menuName = event.target.menu_name.value.trim();
    const menuUrl = event.target.menu_url.value.trim();
    const use_yn = event.target.use_yn.checked ? 'Y' : 'N'; //   
    if (!menuCode) {
      alert(' .')
      return;
    }

    if (!menuName) {
      alert(' .');
      return;
    }

    if (!menuUrl) {
      alert('URL .');
      return;
    }

    const formData = new FormData(event.target);
    formData.set('use_yn', use_yn); //   'Y'  'N' 
    formData.set('org_menu_id', orgMenuInfo.top_menu_id);
    const data = Object.fromEntries(formData.entries());
    try {
      const response = await axios.put(`http://localhost:8080/api/admin/menuMenegement/${menuInfo.menu_id}`, data);
      alert(response.data.resultmessage);
      updateModalClose();
      fetchMenuTree();  //  
      fetchMenuDetail(); //  
    } catch (error) {
      console.error('     :', error);
      alert('      .');
    }
  };

  // api  
  const handleDelete = async () => {
    try {
      const response = await axios.delete(`http://localhost:8080/api/admin/menuMenegement/${menuInfo.menu_id}`);
      alert(response.data.resultmessage);
      //    (:  ,   )
      fetchMenuTree();  //  
      deleteModalClose();
    } catch (error) {
      console.error('    :', error);
      alert('     .');
    }
  };

  return (
    <div>
      <div className="tittle-ctn3">
        <div></div>
      </div>
      <div className="maincontents-ctn3">
        <div className="left-tree-ctn3">
          <div className="title-div-ctn3"><span></span><Button primary onClick={insertModalShow}></Button></div>
          <div className="contents-tree-ctn3">
          <Accordion alwaysOpen>
            {MenuTreeList.map((menu, index) => (
                <Accordion.Item eventKey={index.toString()} key={menu.top_menu_id}>
                    <Accordion.Header onClick={() => handleTopMenuClick(menu.top_menu_id, menu.top_menu_name)}>{menu.top_menu_name}</Accordion.Header>
                    <Accordion.Body>
                        {menu.child_munu_name_list.length > 0 ? (
                            menu.child_munu_name_list.map((childMenu, childIndex) => (
                                <div key={menu.child_menu_id_list[childIndex]}>
                                    <span onClick={() => handleChildMenuClick(menu.child_menu_id_list[childIndex],childMenu)}>{childMenu}</span>
                                    {childIndex < menu.child_munu_name_list.length - 1 && <hr />}
                                </div>
                            ))
                        ) : (
                            null
                        )}
                    </Accordion.Body>
                </Accordion.Item>
            ))}
          </Accordion>
          </div>
        </div>

        <div className="right-details-ctn3">
          <div className="title-div-ctn3"><span> </span><Button primary onClick={() => updateModalShow(menuInfo.menu_id)} ></Button> <Button primary onClick={() => deleteModalShow(menuInfo.menu_id)}></Button></div>
          <div className="contents-details-ctn3">
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> ID</div>
              <div className="item-value-ctn3">{MenuDetail.menu_id ? MenuDetail.menu_id : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> CODE</div>
              <div className="item-value-ctn3">{MenuDetail.menu_code ? MenuDetail.menu_code : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> </div>
              <div className="item-value-ctn3">{MenuDetail.menu_name ? MenuDetail.menu_name : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> </div>
              <div className="item-value-ctn3">{MenuDetail.menu_seq ? MenuDetail.menu_seq : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> </div>
              <div className="item-value-ctn3">{MenuDetail.org_menu_id ? MenuDetail.org_menu_id : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> </div>
              <div className="item-value-ctn3">{MenuDetail.use_yn ? MenuDetail.use_yn : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"> URL</div>
              <div className="item-value-ctn3">{MenuDetail.menu_url ? MenuDetail.menu_url : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"></div>
              <div className="item-value-ctn3">{MenuDetail.create_dt ? MenuDetail.create_dt : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"></div>
              <div className="item-value-ctn3">{MenuDetail.create_user ? MenuDetail.create_user : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"></div>
              <div className="item-value-ctn3">{MenuDetail.modify_dt ? MenuDetail.modify_dt : '-'}</div>
            </div>
            <div className="detail-item-ctn3">
              <div className="item-label-ctn3"></div>
              <div className="item-value-ctn3">{MenuDetail.modify_user ? MenuDetail.modify_user : '-'}</div>
            </div>
          </div>  
        </div>

      </div>
      
      {/*   */}
      <Modal isOpen={insertModalOpen} 
             onRequestClose={insertModalClose} 
             style={InsertModalStyles} 
             ariaHideApp={false} 
             contentLabel="Pop up Message" 
             shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2>   </h2>
          <form onSubmit={handleInsert}>
            <GridContainer>
                <FormItem key="1">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="" name="menu_code"/>
                </FormItem>
                <FormItem key="2">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="" name="menu_name"/>
                </FormItem>
                <FormItem key="3">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Input type="text" id="" name="menu_seq" onChange={handleMenuSeqChange}/>
                </FormItem>
                <FormItem key="4">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                  <Input type="text" disabled id="" name="org_menu_id" value={orgMenuInfo.top_menu_name}/>
                </FormItem>
                <FormItem key="5">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                  <Input type="checkbox" id="" name="use_yn" />
                </FormItem>
                <FormItem key="6">
                  <LabelDiv>
                    <Label htmlFor="URL">URL</Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="URL" name="menu_url"/>
                </FormItem>
            </GridContainer>
            <ButtonContainer>
              <Button primary type="submit"></Button>
              <Button onClick={insertModalClose}></Button>
            </ButtonContainer>
          </form>
        </div>
      </Modal>


      {/*   */}
      <Modal
        isOpen={updateModalOpen}
        onRequestClose={updateModalClose}
        style={InsertModalStyles}
        ariaHideApp={false}
        contentLabel="Pop up Message"
        shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2>   </h2>
          <form onSubmit={handleUpdate}>
            <GridContainer>
                <FormItem key="1">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="" name="menu_code" value={MenuDetail.menu_code} onChange={handleInputChange}/>
                </FormItem>
                <FormItem key="2">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="" name="menu_name" value={MenuDetail.menu_name} onChange={handleInputChange}/>
                </FormItem>
                <FormItem key="3">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                    <Input type="text" id="" name="menu_seq" value={MenuDetail.menu_seq} onChange={handleMenuSeqChange_handleInputChange}/>
                </FormItem>
                <FormItem key="4">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                  <Input type="text" disabled id="" name="org_menu_id" value={MenuDetail.org_menu_id} onChange={handleInputChange}/>
                </FormItem>
                <FormItem key="5">
                  <LabelDiv>
                    <Label htmlFor=""></Label>
                  </LabelDiv>
                  <Input type="checkbox" id="" name="use_yn" value={MenuDetail.menu_use_yn} onChange={handleInputChange}/>
                </FormItem>
                <FormItem key="6">
                  <LabelDiv>
                    <Label htmlFor="URL">URL</Label>
                    <Required>*</Required>
                  </LabelDiv>
                  <Input type="text" id="URL" name="menu_url" value={MenuDetail.menu_url} onChange={handleInputChange}/>
                </FormItem>
            </GridContainer>
            <ButtonContainer>
              <Button primary type="submit"></Button>
              <Button onClick={updateModalClose}></Button>
            </ButtonContainer>
          </form>
        </div>
      </Modal>

      {/*   */}
      <Modal
        isOpen={deleteModalOpen}
        onRequestClose={deleteModalClose}
        style={DeleteModalStyles}
        ariaHideApp={false}
        contentLabel="Pop up Message"
        shouldCloseOnOverlayClick={false}
      >
        <div>
          <h2>  </h2>
          {menuInfo.menu_id} {menuInfo.menu_name} ?
          <ButtonContainer>
            <Button primary onClick={handleDelete}></Button>
            <Button onClick={deleteModalClose}></Button>
          </ButtonContainer>
        </div>
      </Modal>
      


      

      
      
    </div>
  );
}

export default MenuManagement;